.footer {
    background-color: #2D2D2D;
    min-height: 10vh;
    width: 100%;
    padding-bottom: 1rem;
}

.footer-social {
    display: inline-flex;
    gap: 4rem;
}

@media screen and (max-width: 428px) {
    .footer-social {
        display: inline-flex;
        gap: 1rem;
    }
}